.input_block {
  border-radius: 12px;
  width: 100%;
  padding: 12px 20px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  display: flex;
  column-gap: 16px;
  align-items: center;

  .label_and_input_and_eye {
    display: flex;
    justify-content: space-between;
    width: 100%;

    .label_and_input {
      padding-left: 16px;
      border-left: 1px solid rgba(255, 255, 255, 0.25);
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 88%;

      .label {
        padding-left: 2px;
        color: #B5B6B6;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: -0.24px;
        width: 100%;
      }

      input,
      .input {
        width: 100%;
        background: none;
        border: none;
        color: white;
        outline: none;
        font-size: 15px;
        font-weight: 500;
        line-height: 20px;
        overflow: auto;
        padding-bottom: 3px;

        @media (max-width: 500px) {
          font-size: 12px;
        }
      }

      input:-internal-autofill-selected {
        background-color: none !important;
        color: white !important;
        font-size: 15px !important;
        font-weight: 500 !important;

        @media (max-width: 500px) {
          font-size: 12px !important;
        }
      }

      input:-webkit-autofill,
      input:-webkit-autofill:focus {
        transition: background-color 600000s 0s, color 600000s 0s;
      }

      input::-webkit-scrollbar-track,
      .input::-webkit-scrollbar-track {
        background-color: rgba(255, 255, 255, 0.1);
        border-radius: 12px;
      }

      input::-webkit-scrollbar-thumb,
      .input::-webkit-scrollbar-thumb {
        background-color: rgba(255, 255, 255, 0.3);
        border-radius: 12px;
      }

      input::-webkit-scrollbar,
      .input::-webkit-scrollbar {
        height: 6px;
      }

      .dog_and_input {
        display: flex;
        align-items: center;

        .dog {
          font-size: 15px;
          font-weight: 500;
          line-height: 20px;
        }

        input {
          padding-top: 6px;
        }
      }
    }

    .eye {
      transition: 300ms;
    }

    .eye:hover {
      cursor: pointer;
      transform: rotate(10deg) scale(1.2);
    }
  }
}

