}

.send_info_button {
  padding: 19px 44px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 65px;
  background: var(--primary, #3763FF);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -0.32px;
  width: 100%;
  max-width: 230px;
  transition: background 200ms, color 200ms;
}

.section {
  border-radius: 14px;
  background: rgba(255, 255, 255, 0.05);
  padding: 20px 26px 30px;
  width: 100%;

  .hr {
    margin-top: 24px;
    width: 100%;
    height: 1px;
    background: linear-gradient(270deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.50) 52.60%, rgba(255, 255, 255, 0.00) 100%);
  }

  .content {
    margin-top: 24px;
  }
}

.buttons_balance {
  display: flex;
  column-gap: 12px;

  @media (max-width: 700px) {
    justify-content: center;
    width: 100%;
  }

  .button {
    padding: 16px;
    border-radius: 20px;
    transition: background 200ms;
    background: var(--5, rgba(255, 255, 255, 0.05));
    padding-right: 100px;

    @media (max-width: 600px) {
      padding-right: 16px;
      width: 100%;
      padding: 10px;
    }

    .name {
      margin-top: 12px;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: -0.36px;

      @media (max-width: 500px) {
        font-size: 13px;
      }
    }

    .desc {
      margin-top: 6px;
      color: var(--secondary, #B5B6B6);
      font-size: 15px;
      font-weight: 500;
      line-height: 20px;

      @media (max-width: 500px) {
        font-size: 12px;
      }
    }
  }

  .button:hover {
    opacity: 1;
    cursor: pointer;
    background: var(--5, rgba(255, 255, 255, 0.15));
  }
}

.checkbox {
  display: flex;
  column-gap: 8px;

  .checkbox_wrapper,
  .text {
    margin: auto 0;
  }

  .checkbox_wrapper:hover {
    border: 1px solid rgba(255, 255, 255, 0.5);
    cursor: pointer;
  }

  .checkbox_wrapper {
    border: 1px solid rgba(255, 255, 255, 0.25);
    display: flex;
    justify-content: center;
    transition: border 200ms;
    border-radius: 7px;
    width: 24px;
    height: 24px;

    img {
      display: none;
    }
  }

  .checked {
    background: var(--primary, #3763FF);
    border: none;

    img {
      display: block;
      user-select: none;
    }
  }

  .text {
    color: var(--secondary, #B5B6B6);
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    a {
      font-size: 14px;
      line-height: 20px;
      font-weight: 700;
      letter-spacing: -0.28px;
      color: #d9e44f;
    }
  }
}



.modal {
  background: linear-gradient(180deg, rgba(11, 13, 27, 0.9) 0%, rgba(0, 0, 0, 0.6) 100%);
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  height: 100vh;
  padding: 15px;
  display: none;
  z-index: 10;
  justify-content: center;
  overflow: auto;
  .modal_closer:hover {
    transform: rotate(10deg) scale(1.1);
  }

  .modal_closer {
    transition: transform 100ms;
    cursor: pointer;
  }

  .modal_inner {
	margin-top: 4vh;
	margin-bottom: 4vh;
    width: 100%;
    height: auto;
    border-radius: 14px;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: var(--5, rgba(30, 30, 30, 0.5));
    backdrop-filter: blur(34px);
    padding: 24px;
	@media (max-width: 700px) {
		padding: 16px;
	}
    .header {
      display: flex;
      justify-content: space-between;
      column-gap: 50px;

      .name,
      img {
        margin: auto 0;
      }

      .name {
        display: flex;
        column-gap: 16px;

        img,
        .text {
          margin: auto 0;
        }

        .text {
          font-size: 26px;
          font-weight: 700;
          letter-spacing: -0.52px;
		  @media (max-width: 700px) {
			  font-size: 18px;
			  
		  }
        }
      }
    }
  }

  #inner_select_your_wallet {
    width: 500px;

    @media (max-width: 550px) {
      width: 100%;
    }

    .content {
      .description {
        color: rgba(255, 255, 255, 0.50);
        font-size: 15px;
        font-weight: 500;
        margin-top: 10px;
      }

      .wallet_type {
        margin-top: 8px;
        display: flex;
        column-gap: 18px;
        align-items: center;
        transition: border 200ms;
        width: 100%;
        padding: 16px;
        border-radius: 12px;
        border: 1px solid var(--5, rgba(255, 255, 255, 0.05));
        background: var(--5, rgba(255, 255, 255, 0.05));
        height: 56px;

        @media (max-width: 350px) {
          padding: 10px;
          column-gap: 10px;
        }

        .border,
        .img {
          @media (max-width: 600px) {
            margin: auto 0;
          }
        }

        .border {
          border-radius: 27px;
          border: 1px solid var(--white-25, rgba(255, 255, 255, 0.25));
          width: 24px;
          height: 24px;
        }
      }

      .wallet_type:hover {
        border: 1px solid #3763FF;
        cursor: pointer;
      }

      .wallet_type_first {
        margin-top: 24px;
      }

      .selected {
        background: rgba(109, 141, 255, 0.05);

        .border {
          border-radius: 27px;
          border: 8px solid var(--primary, #3763FF);
          background: var(--white, #FFF);
          width: 24px;
          height: 24px;
        }
      }

      .or {
        display: flex;
        margin-top: 24px;
        column-gap: 26px;
        align-items: center;

        .line {
          height: 1px;
          width: 100%;
          background: rgba(255, 255, 255, 0.25);
        }
      }

      .accept_btn {
        margin-top: 24px;
      }
    }
  }

  #inner_loader {
    
    .modal_inner {
      background: none;
    }

    img {
      width: 100px;
      height: 100px;
      animation: loader 1s infinite cubic-bezier(0.5, 0, 0.5, 1);
    }

    @keyframes loader {
      0% {
        transform: rotate(0deg);
      }

      80% {
        transform: rotate(360deg);
      }

      100% {
        transform: rotate(360deg);
      }
    }
  }

  #inner_pay {
    max-width: 487px;

    .input_block {
      margin-top: 24px;
    }

    .ccv_and_date {
      display: flex;
      margin-top: 12px;
      justify-content: space-between;

      @media (max-width: 550px) {
        flex-wrap: wrap;
        row-gap: 12px;
      }

      .input_block {
        margin-top: 0px;
        width: 48%;
      }
    }

    .price {
      margin-top: 24px;
      border-radius: 12px;
      background: var(--5, rgba(255, 255, 255, 0.05));
      padding: 23px;
      display: flex;
      justify-content: space-between;

      .name {
        font-size: 15px;
        font-weight: 500;
        line-height: 20px;
        color: var(--secondary, #B5B6B6);

        @media (max-width: 550px) {
          font-size: 14px;
        }
      }

      .new_old {
        display: flex;
        column-gap: 12px;
        margin-top: 8px;

        .new,
        .old {
          margin: auto 0;
        }

        .new {
          font-size: 24px;
          font-weight: 700;
          letter-spacing: -0.48px;

          @media (max-width: 550px) {
            font-size: 17px;
          }
        }

        .old {
          color: rgba(255, 255, 255, 0.50);
          font-size: 20px;
          font-weight: 300;
          letter-spacing: -0.4px;
          text-decoration: line-through;

          @media (max-width: 550px) {
            font-size: 14px;
          }
        }
      }

      .num,
      .percent {
        margin: auto 0;
      }

      .percent {
        color: var(--frames, #1C1D1E);
        font-size: 15px;
        font-weight: 500;
        line-height: 20px;
        border-radius: 6px;
        background: var(--accent, #ACE44F);
        padding: 4px 12px;

        @media (max-width: 550px) {
          font-size: 14px;
        }
      }
    }

    .checkbox {
      margin-top: 24px;

      a {
        color: rgba(217, 228, 79, 1);
        text-decoration: none;
      }
    }

    .accept_btn {
      margin-top: 24px;
    }
  }

  #inner_add_api {
    max-width: 678px;
    @media (max-width: 750px) {
      max-width: 95vw;
    }
    .content {
      .accept_btn_form{
        width: 40%;
        @media (max-width: 600px) {
          width: 100%;
        }
      }
      .steps_and_notice {
        display: flex;
        justify-content: space-between;
        margin-top: 28px;
        padding-top: 20px;
        flex-wrap: wrap;
        border-top: 1px solid rgba(255, 255, 255, 0.25);

