            border-radius: 11px;
            background: var(--5, rgba(255, 255, 255, 0.05));
            padding: 14px;

            .text {
              margin-top: 16px;

              .header {
                font-size: 18px;
                font-weight: 700;
                letter-spacing: -0.36px;
              }

              .info {
                margin-top: 8px;
                color: var(--secondary, #B5B6B6);
                font-size: 15px;
                font-weight: 500;
                line-height: 20px;
              }
            }
          }
        }

        .steps_and_bg {
          display: flex;
          width: 360px;

          @media (max-width: 700px) {
            width: 100%;
            flex-wrap: wrap;
          }

          .bg {
            width: 58px;
            border-radius: 67px;
            background: linear-gradient(180deg, #287EFF 0%, #4200FF 34.90%, #AB1AFF 67.71%, rgba(188, 31, 255, 0.00) 100%);

            @media (max-width: 700px) {
              width: 100%;
              height: 62px;
              background: linear-gradient(90deg, #287EFF 0%, #4200FF 34.90%, #AB1AFF 67.71%, rgba(188, 31, 255, 0.00) 100%);
            }
          }

          .steps {
            width: 80%;
            margin-top: 4px;
            margin-left: -54px;
            display: flex;
            flex-wrap: wrap;

            @media (max-width: 700px) {
              width: 100%;
              flex-wrap: nowrap;
              margin: 0px;
              margin-top: -56px;
            }

            .step:not(:first-child) {
              margin-top: 50px;

              @media (max-width: 700px) {
                margin-top: 0px;
              }
