                  font-size: 18px;
                  font-weight: 700;
                  letter-spacing: -0.36px;

                  @media (max-width: 700px) {
                    text-align: center;
                    width: 100%;
                    margin-top: 18px;
                    font-size: 17px;
                  }
                }

                .info {
                  color: var(--secondary, #B5B6B6);
                  font-size: 15px;
                  font-weight: 500;
                  line-height: 20px;
                  margin-top: 8px;

                  @media (max-width: 700px) {
                    margin-top: 6px;
                    text-align: center;
                    font-size: 12px;
                  }
                }
              }

              .num,
              .text {
                margin: auto 0;

                @media (max-width: 700px) {
                  margin: 0;
                }
              }
            }

            .step:first-child {
              @media (max-width: 700px) {
                flex-wrap: wrap;
                justify-content: left;

                button {
                  margin-left: 6px;
                }
              }
            }

            .step:last-child {
              @media (max-width: 700px) {
                flex-wrap: wrap;
                justify-content: right;

                button {
                  margin-right: 6px;
                }
              }
            }
          }
