    @media (max-width: 900px) {
      padding: 20px 10px;
    }

    .table {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .send_info_button {
        display: none;

        @media (max-width: 900px) {
          display: block;
          margin-top: 16px;
        }
      }

      .element:first-child {
        background: none;
        margin-top: 20px;

        @media (max-width: 900px) {
          display: none;
        }

        .num,
        .type,
        .date,
        .status,
        .sum {
          font-size: 14px;
          font-weight: 400;
          color: rgba(255,255,255,0.7);
        }
      }

      .element {
        margin-top: 6px;
        display: flex;
        justify-content: space-between;
        width: 100%;
        border-radius: 12px;
        background: rgba(255, 255, 255, 0.03);
        padding: 12px 25px;
        flex-wrap: wrap;

        @media (max-width: 900px) {
          margin-top: 12px;
          padding: 20px;
        }

        .name {
          display: none;

          @media (max-width: 900px) {
            display: block;
            opacity: 0.7;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            margin: auto 0;
            color: white;
          }
        }

        .success {
          background: #2CBA65;
        }

        .error {
          background: #FF5F72;
        }

        .num {
          width: 5.5%;
          font-size: 16px;
          font-weight: 700;
          letter-spacing: -0.32px;
          color: rgba(255, 255, 255, 0.5);
        }

        .type {
          width: 28.3%;
          display: flex;
          column-gap: 12px;

          .text,
          img {
            margin: auto 0;
          }

          .text {
            font-size: 15px;
            font-weight: 500;

            @media (max-width: 900px) {
              text-align: right;
            }
          }

          img {
            @media (max-width: 900px) {
              display: none;
            }
          }
        }

        .date {
          width: 13.6%;
          font-size: 15px;
          font-weight: 500;
          color: rgba(255, 255, 255, 0.7);
        }

        .status {
          width: 33.9%;

          .success,
          .error {
            font-size: 15px;
            font-weight: 500;
            line-height: 20px;
            padding: 8px 16px;
            border-radius: 24px;
          }
        }

        .sum {
          width: 12.8%;

          .text {
            column-gap: 16px;
            display: flex;

            .count,
            .percent {
              margin: auto 0;
            }

            .count {
              font-size: 15px;
              font-weight: 700;
              letter-spacing: -0.375px;
            }

            .percent {
              border-radius: 31px;
              font-size: 15px;
              font-weight: 500;
              line-height: 20px;
              padding: 4px 12px;
            }
          }
        }

        .num,
        .type,
        .date,
        .status,
        .sum {
          margin: auto 0;

          @media (max-width: 900px) {
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding: 8px 0px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.05);
          }
        }
      }
    }

    .security_and_pages {
      display: flex;
      justify-content: space-between;
      margin-top: 23px;
      column-gap: 100px;

      @media (max-width: 900px) {
        display: none;
      }

      .pages,
      .secure {
        margin: auto 0;
      }

      .pages {
        display: flex;
        column-gap: 8px;

        button {
          width: 48px;
          height: 48px;
          background: rgba(255, 255, 255, 0.05);
          border-radius: 12px;
          font-style: normal;
          font-weight: 500;
          font-size: 15px;
          line-height: 20px;
        }

        .active {
          background: #3763FF;
        }
      }
    }
  }

