      background: black linear-gradient(0deg, rgba(18, 70, 255, 0.68) 0%, rgba(18, 47, 147, 0.00) 100%);
      min-height: auto;
      height: 500px;
      display: flex;
      justify-content: center;
    }

    .line {
      left: -34px;
      top: -39px;

      @media (max-width: 1050px) {
        left: -100px;
      }

      @media (max-width: 800px) {
        left: -365px;
        top: -25px;

        img {
          width: 600px;
        }
      }
    }

    .shadow {
      left: -44px;
      top: 535px;
      animation: shadow_anim 12s infinite alternate ease-in-out;

      @media (max-width: 1050px) {
        left: -140px;
      }

      @media (max-width: 800px) {
        left: -259px;
        top: 313px;
      }

      @keyframes shadow_anim {
        0% {
          transform: translate(0px, 0px);
        }

        100% {
          transform: translate(90px, 10px);
        }
      }
    }

    .safe {
      left: 80px;
      top: 333px;
      animation: safe_anim 12s infinite alternate ease-in-out;

      @media (max-width: 1050px) {
        left: -40px;
      }

      @media (max-width: 800px) {
        left: -150px;
        top: 157px;

        img {
          width: 300px;
        }
      }

      @keyframes safe_anim {
        0% {
          transform: translate(0px, 0px) rotate(0deg);
        }

        100% {
          transform: translate(90px, 10px) rotate(10deg);
        }
      }
    }

    .rotate {
      animation: rotate_anim 400ms cubic-bezier(0.8, -0.3, 0.2, 1.3) alternate;

      @keyframes rotate_anim {
        0% {
          transform: rotate(0deg);
        }

        100% {
          transform: rotate(1080deg);
        }
      }
    }

    .eth {
      left: 297px;
      top: 255px;
      animation: eth_anim 14s infinite alternate ease-in-out;

      @media (max-width: 1050px) {
        left: 200px;
      }

      @media (max-width: 800px) {
        left: 9px;
        top: 111px;

        img {
          width: 130px;
        }
      }

      @keyframes eth_anim {
        0% {
          transform: translate(0px, 0px) rotate(0deg);
        }

        100% {
          transform: translate(100px, -100px) rotate(-5deg);
        }
      }
    }

    .btc {
      left: 57px;
      top: -22px;
      animation: btc_anim 20s infinite alternate ease-in-out;

      @media (max-width: 1050px) {
        left: -50px;
      }

      @media (max-width: 800px) {
        left: -219px;
        top: 20px;

        img {
          width: 210px;
        }
      }

      @keyframes btc_anim {
        0% {
          transform: translate(0px, 0px) rotate(0deg);
        }

        100% {
          transform: translate(-20px, 90px) rotate(8deg);
        }
      }
    }
  }
}

