// STATISTIC

#chart {
  padding: 20px;

  .header {
    font-size: 20px;
    font-weight: 700;
    letter-spacing: -0.4px;
  }

  .lines_and_prices {
    display: flex;
    flex-wrap: wrap;
    row-gap: 32px;
    margin-top: 32px;

    .line_and_price {
      display: flex;
      column-gap: 20px;
      align-items: center;
      width: 100%;

      .price {
        color: var(--secondary, #B5B6B6);
        font-size: 14px;
        font-weight: 400;
        width: 75px;
      }

      .line {
        width: 100%;
        height: 1px;
        border-bottom: 2px dashed rgba(255, 255, 255, .13);
      }
    }
  }

  .times_and_zero {
    margin-top: 32px;
    display: flex;

    .zero {
      width: 75px;
    }

    .times {
      width: 100%;
      display: flex;
      justify-content: space-between;

      @media (max-width: 800px) {
        display: none;
      }
    }

    .time,
    .zero {
      font-size: 14px;
      font-weight: 400;
      color: var(--secondary, #B5B6B6);
    }
  }

  .chart_wrapper {
    height: 0px;
    width: 100%;
    position: relative;
    top: 38px;
    .chart {
      width: 100%;
      padding-left: 88px;
      svg {
        width: 100%;
      }
    }
  }
}

